/**
* @Author: duxin
* description: 首页文章条目
*/

<template>
  <div class="article-wrap">
    <p class="article-title" @click="onTitleClick">BlueLake博客主题的详细配置</p>
    <div class="article-subhead">
      <span>[置顶]&nbsp;|&nbsp;</span>
      <span><i class="iconfont icon-tubiao204"></i> {{data.createTime}}</span>
    </div>
    <p>撒个兑换过撒娇啊实萨达萨达撒阿三大萨达所多打实的撒大所
      打滚就撒回到家后把时间会变得很节萨达撒多萨达撒多阿三大萨达撒多撒多阿三大萨达所多阿三大萨达哀顺变就带把伞大声疾呼不多见啊说不定氨基酸不大好几把是比较大has不接电话把鼠标垫节哀顺变回到家巴萨见不到</p>
  </div>
</template>

<script>
  export default {
    name: "blog-home-article",
    props: {
      data: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    methods: {
      onTitleClick() {
        this.$emit("onItemClick",this.data.mdPath)
      }
    }
  }
</script>

<style scoped>
  .article-wrap {
    width: 700px;
    padding: 25px;
    border-radius: 4px;
    box-shadow: 0 0 20px rgba(206, 211, 200, 1) inset;
    border: 1px solid rgba(206, 211, 200, 1);
    margin-bottom: 40px;
    background: rgba(206, 211, 200, 0.84);
  }

  .article-title {
    color: #40759b;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
  }

  .article-subhead {
    display: flex;
    align-items: center;
    color: #333;
  }

  .iconfont {
    font-size: 18px;
  }
</style>



